.ava{
    width: 30px;
    height: 30px;
    margin: 0 5px;
}
.basket{
    position:relative;
    margin: 0 15px;
}
.basket:hover{
    animation: scale 1.2s infinite linear;
}
.basketItems{
    text-align: center;
    background-color: rgba(255, 255, 0, 0.4);
    border-radius: 50%;
    position: absolute;
    width: 25px;
    height: 25px;
    color: white;
    top: 0;
    right: -5px;
}
@keyframes scale {
    from {
      -webkit-transform: scale(1);
      transform: scale(1);
      opacity: 1;
    }
    50%{
      -webkit-transform: scale(1.3);
      transform: scale(1.3);
      opacity: .6;
    }
  
    to {
      -webkit-transform: scale(1);
      transform: scale(1);
      opacity: 1;
    }
  }